@import url("https://fonts.googleapis.com/css?family=Amatic+SC|Fredericka+the+Great|Lora&display=swap");
html,
body {
  height: 100%; }

body {
  font-family: 'Lora', serif;
  font-size: 16px;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: #FFFFFF; }
  body .container {
    position: relative;
    margin: 0 auto 150px auto;
    max-width: 930px;
