    box-sizing: border-box;
    overflow: hidden;
    padding: 0;
    clear: both; }
    body .container h2 {
      font-family: 'Amatic SC', cursive;
      font-size: 50px;
      color: #777777;
      text-align: center;
      margin: 70px 0 50px 0;
      font-weight: normal; }
    body .container .section {
      min-height: 470px;
      display: block;
      margin: 50px 0 30px;
      overflow: hidden;
      padding: 0; }
    body .container p {
      line-height: 26px;
      color: #666666; }
